import React from "react";
import { graphql } from "gatsby";
import { useTranslation } from "gatsby-plugin-react-i18next";

import Layout from "../components/Layout";


const RecentExperience = () => {
  const { t, i18n } = useTranslation();

  const PREFIX = "cv_experience";

  if (!i18n.exists(`${PREFIX}_title`)) { return (<></>) }

  const retItems = [];
  retItems.push(<h2>{t(`${PREFIX}_title`)}</h2>);

  let curEntry = 1;
  while (i18n.exists(`${PREFIX}_${curEntry}_title`)) {
    retItems.push(<h3>{t(`${PREFIX}_${curEntry}_title`)}</h3>)
    curEntry += 1;
  }

  return (retItems);
}


export default function Cv() {

  const { t } = useTranslation();  // used for showing
 
  return ( 
    <Layout>
      <h1>{t("cv")}</h1>
      <RecentExperience />
      <p>
        {t("cv_content").toString()}
      </p>
    </Layout>
  )
  
}


// this commands defines how the GraphQL query must be executed
// must be present is all pages that use i18next
export const query = graphql`
  query ($language: String!) {
    locales: allLocale(filter: {language: {eq: $language}}) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`;
